@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro:ital,wght@0,200;0,300;0,400;0,700;0,900;1,200;1,300;1,400;1,700;1,900&display=swap);
body {
    font-family: 'Source Code Pro', monospace;
}


.contact-code-block{max-width:70rem}.contact-code-block .code-styled{background:#242424;color:#dedede}.contact-code-block .option-inline{cursor:pointer;color:#242424;background:#7fff00;margin-right:1rem}.contact-code-block .option-inline::before{content:"["}.contact-code-block .option-inline::after{content:"]"}span.code-cursor{animation:cursorblink .65s linear infinite}@keyframes cursorblink{0%{color:inherit}50%{color:transparent}}
